* {
  box-sizing: border-box;
}
button {
  outline: none;
  border: 0;
  cursor: pointer;
}
ul {
  list-style: none;
}

.navbar {
  background-color: blanchedalmond;
  font-size: 2rem;
  padding: 1em;
  /*display: flex;*/
  /*align-items: center;*/
}

.add-form {
  padding: 1rem;
}

.add-input {
  font-size: 2rem;
  margin-right: 0.2em;
}

.add-button,
.habits-reset {
  font-size: 1.5rem;
  height: 100%;
  padding: 0.3em 0.5em;
  background-color: green;
  color: white;
}

.habit-name {
  font-size: 2rem;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  margin-right: 0.5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;
}

.navbar-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: 0.2em;
  background-color: green;
  color: white;
  border-radius: 50%;
}

.habit-button {
  font-size: 2.5rem;
  margin: 0 0.1em;
  background-color: transparent;
}

.habit-button:hover {
  opacity: 0.8;
}

.navbar-logo {
  color: darkgreen;
  margin-right: 0.5em;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  color: darkred;
}